.feedback{
    text-align: center;
    width: max-content;
    margin: 0 auto;
    padding: 20px;
}

.buttonList{
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    width: 350px;
}
.btn {
    display: inline-block;
    padding: 10px 15px;
    background-color: peru;
    color: white;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    width: 90px;
    text-transform: uppercase;
}


.title{
    font-size: 32px;
    color: #691f417e;
}